.vl {
    border-left: 6px solid black;
    height: 500px;
    position: absolute;
    left: 50%;
    margin-left: 0px;
    top: 0;
}
.vlsign {
    border-left: 4px solid black;
    height: 35px;
    position: absolute;
    left: 113%;
    margin-left: 0px;
    top: 0.3;
}
.nav {
    list-style-type: none;
    margin-top: 55px;
    margin-left: -30px;
    white-space: nowrap;
    padding: 0;
    font-size: 23px;
    text-decoration: none;
}
.nav a{
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}
.nav a:hover{
    background-color:transparent;
    text-decoration: underline;
    text-underline-offset: 12px;
    color: black;
}
.nav a.active{
    font-weight: bold;
    text-decoration: underline;
    text-underline-offset: 12px;
}
